'use client';

import { useState } from 'react';
import Image from 'next/image';

interface StyleSelectorProps {
  onStyleSelect: (styleId: string, customDescription?: string) => void;
}

const STYLES = [
  { id: 'gaming', name: 'Gaming Paradise', emoji: 'üéÆ', preview: '/style-previews/gaming.jpg' },
  { id: 'sports_bar', name: 'Sports Bar', emoji: 'üèà', preview: '/style-previews/sports-bar.png' },
  { id: 'home_theater', name: 'Home Theater', emoji: 'üé¨', preview: '/style-previews/home-theater.png' },
  { id: 'workshop', name: 'Workshop', emoji: 'üî®', preview: '/style-previews/workshop.jpg' },
  { id: 'retro_arcade', name: 'Retro Arcade', emoji: 'üëæ', preview: '/style-previews/retro-arcade.jpg' },
  { id: 'whiskey_lounge', name: 'Whiskey Lounge', emoji: 'ü•É', preview: '/style-previews/whiskey-lounge.jpg' },
  { id: 'golf_simulator', name: 'Golf Simulator', emoji: '‚õ≥', preview: '/style-previews/golf-simulator.jpg' },
  { id: 'poker_room', name: 'Poker Room', emoji: '‚ô†Ô∏è', preview: '/style-previews/poker-room.jpg' },
  { id: 'music_studio', name: 'Music Studio', emoji: 'üé∏', preview: '/style-previews/music-studio.jpg' },
  { id: 'industrial_loft', name: 'Industrial Loft', emoji: 'üè≠', preview: '/style-previews/industrial-loft.jpg' },
  { id: 'cigar_lounge', name: 'Cigar Lounge', emoji: 'üö¨', preview: '/style-previews/cigar-lounge.jpg' },
  { id: 'home_office', name: 'Man Cave Office', emoji: 'üíº', preview: '/style-previews/home-office.jpg' },
];

export default function StyleSelector({ onStyleSelect }: StyleSelectorProps) {
  const [showCustom, setShowCustom] = useState(false);
  const [customDescription, setCustomDescription] = useState('');
  const [hoveredStyle, setHoveredStyle] = useState<string | null>(null);

  return (
    <div className="max-w-6xl mx-auto">
      <h2 className="text-2xl md:text-3xl font-bold mb-2 text-center">Choose Your Man Cave Style</h2>
      <p className="text-gray-400 text-center mb-8">See the possibilities for your space</p>
      
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 mb-6">
        {STYLES.map((style) => (
          <button
            key={style.id}
            onClick={() => onStyleSelect(style.id)}
            onMouseEnter={() => setHoveredStyle(style.id)}
            onMouseLeave={() => setHoveredStyle(null)}
            className="relative bg-gray-800 hover:bg-gray-700 rounded-lg overflow-hidden transition-all duration-300 group h-48 border-2 border-transparent hover:border-orange-500"
          >
            {style.preview && (
              <div className="absolute inset-0 opacity-40 group-hover:opacity-60 transition-opacity">
                <Image
                  src={style.preview}
                  alt={style.name}
                  fill
                  className="object-cover"
                  onError={(e) => {
                    (e.target as HTMLElement).style.display = 'none';
                  }}
                />
              </div>
            )}
            
            <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/40 to-transparent" />
            
            <div className="relative h-full flex flex-col items-center justify-end p-4 text-center">
              <div className="text-4xl mb-2 transform group-hover:scale-110 transition-transform">
                {style.emoji}
              </div>
              <div className="text-sm font-bold text-white">{style.name}</div>
              
              {hoveredStyle === style.id && (
                <div className="absolute inset-0 bg-orange-500/20 border-2 border-orange-500 rounded-lg" />
              )}
            </div>
          </button>
        ))}
        
        <button
          onClick={() => setShowCustom(!showCustom)}
          className="relative bg-gradient-to-br from-purple-900/50 to-purple-700/30 hover:from-purple-800/60 hover:to-purple-600/40 rounded-lg transition-all duration-300 h-48 border-2 border-dashed border-purple-500/50 hover:border-purple-400"
        >
          <div className="relative h-full flex flex-col items-center justify-center p-4 text-center">
            <div className="text-5xl mb-3">‚ú®</div>
            <div className="text-sm font-bold text-white mb-1">Custom Design</div>
            <div className="text-xs text-purple-300">Describe your vision</div>
          </div>
        </button>
      </div>

      {showCustom && (
        <div className="bg-gray-800 p-6 rounded-lg border-2 border-purple-500/30">
          <div className="flex items-center gap-2 mb-4">
            <span className="text-2xl">‚ú®</span>
            <h3 className="text-xl font-bold">Describe Your Custom Vision</h3>
          </div>
          <textarea
            value={customDescription}
            onChange={(e) => setCustomDescription(e.target.value)}
            placeholder="Example: A cozy reading nook with leather chairs, dark wood shelves, warm lighting, and vintage maps on the walls..."
            className="w-full bg-gray-900 text-white p-4 rounded border border-gray-700 focus:border-purple-500 focus:outline-none min-h-32 mb-4"
            maxLength={500}
          />
          <div className="flex justify-between items-center">
            <span className="text-sm text-gray-400">{customDescription.length}/500 characters</span>
            <div className="flex gap-3">
              <button
                onClick={() => {
                  setShowCustom(false);
                  setCustomDescription('');
                }}
                className="text-gray-400 hover:text-white px-4 py-2 rounded font-medium"
              >
                Cancel
              </button>
              <button
                onClick={() => {
                  if (customDescription.length > 20) {
                    onStyleSelect('custom', customDescription);
                  } else {
                    alert('Please write at least 20 characters to describe your vision');
                  }
                }}
                disabled={customDescription.length < 20}
                className="bg-purple-500 hover:bg-purple-600 disabled:bg-gray-600 disabled:cursor-not-allowed px-6 py-2 rounded font-medium transition-colors"
              >
                Use Custom Design ‚Üí
              </button>
            </div>
          </div>
        </div>
      )}

      <div className="mt-12 text-center">
        <p className="text-sm text-gray-500">
          üí° Tip: Upload a photo of your current space, pick a style, and watch Nano Banana Pro transform it!
        </p>
      </div>
    </div>
  );
}
