'use client';

import { useState, useRef } from 'react';

interface PhotoUploadProps {
  onPhotoSelect: (imageData: string) => void;
}

export default function PhotoUpload({ onPhotoSelect }: PhotoUploadProps) {
  const [preview, setPreview] = useState<string>('');
  const [isDragging, setIsDragging] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFile = async (file: File) => {
    if (!file.type.startsWith('image/')) {
      alert('Please upload an image file');
      return;
    }

    // Compress image
    const reader = new FileReader();
    reader.onload = (e) => {
      const img = new Image();
      img.onload = () => {
        const canvas = document.createElement('canvas');
        let width = img.width;
        let height = img.height;
        
        const MAX_WIDTH = 1920;
        if (width > MAX_WIDTH) {
          height = (height * MAX_WIDTH) / width;
          width = MAX_WIDTH;
        }
        
        canvas.width = width;
        canvas.height = height;
        const ctx = canvas.getContext('2d');
        ctx?.drawImage(img, 0, 0, width, height);
        
        const compressed = canvas.toDataURL('image/jpeg', 0.85);
        setPreview(compressed);
        onPhotoSelect(compressed);
      };
      img.src = e.target?.result as string;
    };
    reader.readAsDataURL(file);
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
    const file = e.dataTransfer.files[0];
    if (file) handleFile(file);
  };

  return (
    <div className="max-w-2xl mx-auto">
      {!preview ? (
        <div
          onDragOver={(e) => { e.preventDefault(); setIsDragging(true); }}
          onDragLeave={() => setIsDragging(false)}
          onDrop={handleDrop}
          className={`border-2 border-dashed rounded-lg p-12 text-center cursor-pointer transition ${
            isDragging ? 'border-orange-500 bg-orange-500/10' : 'border-gray-600 hover:border-orange-500'
          }`}
          onClick={() => fileInputRef.current?.click()}
        >
          <div className="text-6xl mb-4">üì∏</div>
          <h3 className="text-xl font-bold mb-2">Upload Your Room Photo</h3>
          <p className="text-gray-400 mb-4">Click to browse or drag and drop</p>
          <p className="text-sm text-gray-500">Supports: JPG, PNG (Max 10MB)</p>
          <input
            ref={fileInputRef}
            type="file"
            accept="image/*"
            onChange={(e) => {
              const file = e.target.files?.[0];
              if (file) handleFile(file);
            }}
            className="hidden"
          />
        </div>
      ) : (
        <div>
          <img src={preview} alt="Preview" className="rounded-lg mb-4 w-full" />
          <button
            onClick={() => {
              setPreview('');
              onPhotoSelect('');
            }}
            className="text-gray-400 hover:text-white"
          >
            ‚Üê Change Photo
          </button>
        </div>
      )}

      <div className="mt-6 bg-blue-900/20 p-4 rounded-lg">
        <h4 className="font-bold mb-2">üìù Tips for Best Results:</h4>
        <ul className="text-sm text-gray-400 space-y-1">
          <li>‚úì Take photo from corner of room</li>
          <li>‚úì Include walls, floor, and ceiling</li>
          <li>‚úì Use good lighting</li>
          <li>‚úì Clear the space of clutter</li>
        </ul>
      </div>
    </div>
  );
}
