'use client';

import { useState } from 'react';
import PhotoUpload from '../../../components/PhotoUpload';
import StyleSelector from '../../../components/StyleSelector';

type Step = 'upload' | 'style' | 'generate' | 'results';

export default function GeneratorCompletePage() {
  const [step, setStep] = useState<Step>('upload');
  const [imageData, setImageData] = useState<string>('');
  const [selectedStyle, setSelectedStyle] = useState<string>('');
  const [customDescription, setCustomDescription] = useState<string>('');
  const [isGenerating, setIsGenerating] = useState(false);
  const [generatedImageUrl, setGeneratedImageUrl] = useState<string>('');
  const [error, setError] = useState<string>('');

  const handlePhotoSelect = (data: string) => {
    setImageData(data);
    if (data) setStep('style');
  };

  const handleStyleSelect = (styleId: string, customDesc?: string) => {
    setSelectedStyle(styleId);
    if (customDesc) setCustomDescription(customDesc);
    setStep('generate');
  };

  const handleGenerate = async () => {
    setIsGenerating(true);
    setError('');

    try {
      const response = await fetch('/api/generate-image', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          imageBase64: imageData,
          styleId: selectedStyle,
          customDescription: customDescription || undefined,
        }),
      });

      const data = await response.json();

      if (data.success) {
        setGeneratedImageUrl(data.generatedImageUrl);
        setStep('results');
      } else {
        setError(data.error || 'Generation failed');
      }
    } catch (err: any) {
      setError(err.message || 'Network error');
    } finally {
      setIsGenerating(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-900 text-white p-4 md:p-8">
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-8">
          <h1 className="text-4xl md:text-5xl font-bold mb-2">Man Cave AI Generator</h1>
          <p className="text-gray-400">Transform Your Room with Nano Banana Pro</p>
        </div>

        <div className="flex justify-center gap-2 md:gap-4 mb-12">
          <div className={`px-3 md:px-6 py-2 rounded-lg font-medium ${step === 'upload' ? 'bg-orange-500' : 'bg-gray-800'}`}>1. Upload</div>
          <div className={`px-3 md:px-6 py-2 rounded-lg font-medium ${step === 'style' ? 'bg-orange-500' : 'bg-gray-800'}`}>2. Style</div>
          <div className={`px-3 md:px-6 py-2 rounded-lg font-medium ${step === 'generate' ? 'bg-orange-500' : 'bg-gray-800'}`}>3. Generate</div>
          <div className={`px-3 md:px-6 py-2 rounded-lg font-medium ${step === 'results' ? 'bg-orange-500' : 'bg-gray-800'}`}>4. Results</div>
        </div>

        <div className="bg-gray-800 rounded-lg p-4 md:p-8">
          {step === 'upload' && <PhotoUpload onPhotoSelect={handlePhotoSelect} />}

          {step === 'style' && (
            <div>
              <button onClick={() => setStep('upload')} className="text-gray-400 hover:text-white mb-6">Back to Upload</button>
              <StyleSelector onStyleSelect={handleStyleSelect} />
            </div>
          )}

          {step === 'generate' && (
            <div className="text-center max-w-2xl mx-auto">
              <h2 className="text-2xl font-bold mb-6">Ready to Generate!</h2>
              {imageData && <div className="mb-6"><img src={imageData} alt="Your room" className="rounded-lg max-h-96 mx-auto" /></div>}
              <div className="bg-gray-900 p-4 rounded mb-6 text-left">
                <p className="text-sm text-gray-400 mb-2"><strong>Style:</strong> {selectedStyle === 'custom' ? 'Custom Design' : selectedStyle}</p>
                {customDescription && <p className="text-sm text-gray-400"><strong>Description:</strong> {customDescription}</p>}
              </div>
              {error && <div className="bg-red-900/50 border border-red-500 p-4 rounded mb-6">Error: {error}</div>}
              <button onClick={handleGenerate} disabled={isGenerating} className="bg-orange-500 hover:bg-orange-600 disabled:bg-gray-600 px-8 py-4 rounded-lg text-xl font-bold w-full md:w-auto mb-4">
                {isGenerating ? 'Generating...' : 'Generate My Man Cave!'}
              </button>
              <div><button onClick={() => setStep('style')} className="text-gray-400 hover:text-white">Change Style</button></div>
            </div>
          )}

          {step === 'results' && (
            <div className="text-center">
              <h2 className="text-3xl font-bold mb-6">Your Man Cave Design!</h2>
              <div className="grid md:grid-cols-2 gap-4 mb-6">
                <div>
                  <h3 className="text-sm font-bold mb-2 text-gray-400">BEFORE</h3>
                  <img src={imageData} alt="Before" className="rounded-lg" />
                </div>
                <div>
                  <h3 className="text-sm font-bold mb-2 text-orange-500">AFTER</h3>
                  <img src={generatedImageUrl} alt="After" className="rounded-lg" />
                </div>
              </div>
              <div className="flex gap-4 justify-center flex-wrap">
                <button onClick={() => { setStep('upload'); setImageData(''); setGeneratedImageUrl(''); }} className="bg-orange-500 hover:bg-orange-600 px-6 py-3 rounded-lg font-medium">Create Another</button>
                <a href={generatedImageUrl} download="my-man-cave.jpg" className="bg-gray-700 hover:bg-gray-600 px-6 py-3 rounded-lg font-medium inline-block">Download</a>
              </div>
            </div>
          )}
        </div>

        <div className="mt-8 text-center text-sm text-gray-500"><p>Powered by Nano Banana Pro</p></div>
      </div>
    </div>
  );
}
